	module.exports = {"data":{"markdownRemark":{"html":"<h3>Kuberwhat?</h3>\n<p>In one sentence, Kubernetes is a platform for managing containers. Kubernetes is more so the building blocks of an developer platform rather then the typical all-inclusive platform. It does not provide any CI/CD or application level services, it will however help you run these services on Kubernetes itself.</p>\n<h3>So why Kubernetes?</h3>\n<p>The reason why Kubernetes is becoming increasing popular is due to its container-centric nature and flexibility. With its container-centric management approach provides modularity between each deployed application and allows you to deploy, update and scale in a managed and simple manner. The platform also provides flexibility to add practically any service on top of it and has its own package manager <a href=\"https://helm.sh/\">Helm</a> as well. Kubernetes in particular is great at orchestrating microservices and serves as the basis for many cloud based services.</p>\n<h3>Getting Started</h3>\n<h4>Pre-requisites</h4>\n<p>Before we get started please have Minikube and Docker installed.</p>\n<h4>Creating Your First Kubernetes Cluster</h4>","frontmatter":{"date":"April 25, 2018","path":"/kubernetes","title":"An Introduction to Kubernetes"}}},"pathContext":{}}

